import { React } from 'react'

import comp from '../components/CompetenceMap'

export default function Skills({page}) {

    let array = comp;
    
    if(page === "home") {
        array= [
            array[0],array[1],array[2]
    ];
    }

    return (
        <section id="sec-2">
            <h1>Mes comptences </h1>
            <p>Dveloppeur web et mobile polyvalent avec une bonne exprience dans les technologies Symfony, Sylius, PHP, et Reactjs, et une reconversion professionnelle russie du domaine informatique. Passionn par la conception et le dveloppement d'applications performantes et intuitives, je suis dtermin  apporter mes comptences techniques avances et ma crativit pour relever de nouveaux dfis et contribuer  des projets innovants.</p>
            <div className="container flex-wrap" data-aos="zoom-in">
                {array.map(({icon, description, title}) => ( 
                    <div className="competences col-4">
                            <p className="i">{icon}</p>
                            <h2>{title}</h2>
                            <p>{description}</p>
                    </div>
                ))}
            </div>
        </section>
    )
}
